@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
/* base styles */
* {
  margin-top: 0px;
  margin: 0px;
  padding-top: 0px;
  font-family: "Quicksand";
  color: #333;
  
  /*font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;*/
}
.index{
  color: blue;
}
.wrapper
{
  display:grid;
  grid-template-rows: repeat(4, [row] auto  );
  grid-template-columns: auto;
  display: inline-grid;
  border: #272727 2px ;
}
table {
  display: table; 
  border-collapse: collapse;
  box-sizing: border-box;
  text-indent: initial;
  border-spacing: 0px;
  border: 2px;
  width: 100%;
}
a
{
  color: #f1356d;
  text-decoration: none;
  width: auto;
}
a:visited
{
  color: none;
}
a li
{
  color:#f1356d;
}

hr
{
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0,0,0,.1);
 
}
td
{
  vertical-align: top;
  border: solid 1px rgb(205,205,205);
}
li
{
  padding-bottom: 10px;
}
code
{
  /*background-color: rgb(223, 255, 228);*/
  background-color: rgb(54, 54, 54);
  color: white;
  border-radius: 4px;
  padding-left: 3px;
  padding-right: 3px;
  padding-bottom: 3px;
}

/* per i sottotitoli dentro Index dei blog post */
ul ul
{
  font-size: 15px;
  font-style: italic;
}
.Post ul
{
  margin-left: -20px;
}
.Post li
{
  margin-bottom: -8px;
}
.Post
{
  margin-right: 350px;
}
.my-single-menu
{
  background-color: rgb(253, 253, 253);
  font-size: 18px;
  box-shadow: 0 8px 6px -3px rgb(207, 207, 207);
  max-width: 600px;
  height: auto; /* it was 50px now is auto because of the image fit keep image consistent*/
  color: #f1356d;
  background-color: rgb(250, 250, 250);
}
.my-single-menu img
{
  width: 600px;
  height: 200px;
  object-fit: cover;
}
.my-single-menu post-date
{
  font-size: 13px;
}
.content {
  margin-left: 250px;
  margin-right: 0px;
  padding-left: 50px;
  padding-right: 50px;
  
  height: 100%;

  padding-top: 100px;
}

.Post p
{
  margin-top: 15px;
  margin-bottom: 15px;
}
.Post img{
  max-width: 100%;
  border: solid 2px;
  border-color: #d8d8d8;
}
.sidebarLeft  
{
  position:fixed;
  background-color: rgb(250, 250, 250);
  height: 100%;
  width: 250px;
  transition: transform 0.3s ease; /* Added for smooth sliding */
}
.sidebarLeft li:hover 
{
  background-color: #f1356d
}

.sidebarLeft a li:hover 
{
  color: white;
}
.sidebarLeft a li:focus
{
  color: rgb(63, 9, 9);
}

.sidebarLeft .avatar 
{
  margin: auto;
}

.sidebarLeft .avatar img
{
  display: block;
  margin-top: 30px;
  border-radius: 50%;
  height: 170px;
  width: 170px;
  border-color: #272727;
  border-style: solid;
  border-width: 7px;
  margin-left: auto;
  margin-right: auto;
}

.sidebarLeft ul
{
  display: block;
  padding: 0px; /* it is needed */
  padding-top: 30px;
}
.sidebarLeft li
{
  padding-left: 30px;
  padding-top: 15px;
  list-style-type: none;
  height: 30px;
}

/*------BURGER MENU TEST-------*/
/* Sidebar default styling */

/* Burger menu styling */
.burger-menu {
  display: none;
  font-size: 30px;
  cursor: pointer;
  padding: 10px;
  background-color: #f1356d; /* Background color of the button */
  color: #fff; /* Color of the burger icon */
}
/* Media query for small screens */
@media (max-width: 800px) {
  body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
  }
  * {
    margin-top: 0px;
    margin: 0px;
    padding-top: 0px;
    font-family: "Quicksand";
    color: #333;
    /*font-size: medium; TO SET UP
    /*font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;*/
  }
  .Post {
    font-size: large;
  }
  .sidebarLeft .avatar img
{
  display: block;
  margin-top: 30px;
  border-radius: 50%;
  height: 100px;
  width: 100px;
  border-color: #272727;
  border-style: solid;
  border-width: 7px;
  margin-left: auto;
  margin-right: auto;
}
  .burger-menu {
    display: block;
    position: fixed;
    top: 5px;
    left: 5px;
    z-index: 1000; /* Ensures it appears above other elements */
  }

  .sidebarLeft {
    transform: translateX(-100%); /* Hidden by default */
    width: 250px; /* Sidebar width */
    font-size: 25px;
  }
.sidebarLeft li
{
  height: 40px;
  
}

  .sidebarLeft.open {
    transform: translateX(0); /* Slides into view when open */
  }

  /* Adjust content styling when sidebar is open */
  .sidebarLeft ul {
    padding-top: 10px;
  }
}
/*------MENU TEST END--------*/


.sidebarRight
{
  height: 100%;
  width: 300px;
  border-left: #d8d8d8 solid 1px;
  float: right;
} 

.content .sidebarRight .sidebarRightChild
{
  position: fixed;
  height: 80%;
  border-left: #d8d8d8 solid 1px;
  padding-left:  0px;
  padding-right: 0px;
  display: inline-block;
  max-width:350px;
  overflow-wrap: break-word;
}
.sidebarRight .sidebarRightChild .tags
{
  margin-top: 20px;
  display: flex;
  flex-flow: row wrap;
  /*justify-content: space-evenly;*/
}

.sidebarRight .sidebarRightChild my-tag
{
  color: rgb(255, 255, 255);
  background-color: #f1356d;
  border-radius: 8px;
  padding-left: 4px;
  padding-right: 4px;
  padding-top: 2px;
  padding-bottom: 2px;
  margin-left: 3px;
  margin-right: 3px;
  margin-top: 5px;
}

